import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import {
  GoogleMaps,
  GoogleMap,
  GoogleMapsEvent,
  MarkerCluster,
  Marker,
  Environment,
  MyLocation,
  GoogleMapsAnimation
} from "@ionic-native/google-maps";
import { Platform, LoadingController } from '@ionic/angular';
import data from './data';
import { async } from '@angular/core/testing';

@Component({
  selector: 'app-explore',
  templateUrl: './explore.page.html',
  styleUrls: ['./explore.page.scss'],
})
export class ExplorePage {
  @ViewChild('mapRef', {static: false}) mapRef: ElementRef;

  private map: GoogleMap;
  private loading: any;
  private hideMissionCard: boolean = true;

  constructor(
    private platform: Platform,
    private loadingCtrl: LoadingController
  ) { }

  ionViewDidEnter() {
    this.platform.ready().then(async () => {
      console.log(this.mapRef);
      this.mapRef.nativeElement.style.position = 'static';
      await this.loadMap();
    });
  }

  async loadMap() {
    this.loading = await this.loadingCtrl.create({ message: 'Por favor, aguarde...' });
    await this.loading.present();

    Environment.setEnv({
      'API_KEY_FOR_BROWSER_RELEASE': 'AIzaSyBBeiv9AiQcsrRbU1veU95qbaEIsCkcUrY',
      'API_KEY_FOR_BROWSER_DEBUG': 'AIzaSyBBeiv9AiQcsrRbU1veU95qbaEIsCkcUrY'
    });

    this.map = GoogleMaps.create(this.mapRef.nativeElement, {
      'camera': {
        'target': {
          "lat": 21.382314,
          "lng": -157.933097
        },
        'zoom': 10
      },
      'controls': {
        'zoom': false
      },
    });
    this.addCluster(data);
  }

  addCluster(data) {
    let markerCluster: MarkerCluster = this.map.addMarkerClusterSync({
      markers: data,
      icons: [
        {
          min: 3,
          max: 9,
          url: "./assets/markercluster/small.png",
          label: {
            color: "white"
          }
        },
        {
          min: 10,
          url: "./assets/markercluster/large.png",
          label: {
            color: "white"
          }
        }
      ]
    });

    this.map.on(GoogleMapsEvent.MAP_READY).subscribe(async(params) => {
      console.log('MAP READY')
      try {
        const myLocation: MyLocation = await this.map.getMyLocation();
        await this.map.moveCamera({
          target: myLocation.latLng,
          zoom: 18
        });
        await this.map.addMarkerSync({
          title: 'Origem',
          icon: '#000',
          animation: GoogleMapsAnimation.DROP,
          position: myLocation.latLng
        });
        console.log(myLocation);
      } catch (error) {
        console.error(error);
      } finally {
        this.loading.dismiss();
      }
    });

    markerCluster.on(GoogleMapsEvent.MARKER_CLICK).subscribe((params) => {
      let marker: Marker = params[1];
      marker.hideInfoWindow();
      // marker.setTitle(marker.get("name"));
      // marker.setSnippet(marker.get("address"));
      this.hideMissionCard = false;
      console.log(marker);
      // marker.showInfoWindow();
    });
  }

}
